/*** login page ***/
.zmovo-inner-page {
  position: relative;
}

.zmovo-login-input-top input {
  min-height: 46px;
  border-radius: 5px;
  border: 1px solid #161631;
  background-color: transparent;
  margin-bottom: 21px;
}

.zmovo-page-login {
  border: 10px solid #161631;
  padding: 35px;
  border-radius: 5px;
  width: 100%;
}

.zmovo-login-page-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 400px;
  margin: auto;
  max-width: 100%;
}

.zmovo-login-page form {
  text-align: center;
}

.zmovo-login-page .zmovo-logo {
  display: inline-block;
  margin-bottom: 30px;
}

.zmovo-login-fb {
  text-align: center;
}

.zmovo-login-fb>span {
  text-transform: uppercase;
  font-weight: bold;
  display: block;
  margin-bottom: 17px;
}

.zmovo-login-fb ul li {
  display: inline-block;
  margin: 0 10px;
}

.zmovo-login-fb ul li a {
  display: block;
  padding: 4px 12px;
  color: #fff;
  border-radius: 4px;
}

.zmovo-login-fb ul li a span {
  margin-right: 5px;
}

.zmovo-login-btns {
  text-align: center;
  margin: 22px 0;
}

.zmovo-login-btn {
  color: #fff;
  display: inline-block;
  background: var(--color-1);
  padding: 13px 35px;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: bold;
  border: none;
}

.zmovo-login-btn:hover {
  background-color: #f47a0a;
}

.zmovo-forgate {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.zmovo-remember-me {
  display: block;
  text-align: left;
}

.zmovo-remember-me input {
  margin-right: 5px;
}

.zmovo-forgate-me {
  display: inline-block;
  float: right;
}

.zmovo-remember-me label {
  position: relative;
  top: -2px;
}

.zmovo-register-me a {
  color: var(--color-1);
  font-weight: bold;
}

.zmovo-register-me a:hover,
.zmovo-forgate-me a:hover {
  color: var(--color-2);
}

.zmovo-forgate-me a {
  color: #fff;
}

.zmovo-login-input-top input:focus {
  box-shadow: none;
  background-color: transparent;
  border-color: #161631;
  color: #fff;
}

.zmovo-forgate-me a span:hover {
  color: var(--color-1);
}

li.log-fb a {
  background-color: #4367af;
}

li.log-goo a {
  background-color: #e45446;
}

li.log-goo a:hover {
  background-color: #d73c2d;
}

li.log-fb a:hover {
  background-color: #365ba3;
}
.zmovo-page-login .form-group,.zmovo-login-input-top{
  position: relative;
}
.zmovo-page-login .tip{
  position: absolute;
  bottom: 100%;
  right:0;
  color: var(--danger);
  font-size: 12px;
}
.zmovo-page-login .tip#tip-account{
  bottom: inherit;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}